// some customizations for k-cards 

.k-card {
    .k-card-header {
        font-size: $lead-font-size;
        font-weight: 700;
        color: $component-color;
        padding: $header-cell-padding-y $header-cell-padding-x;

        @each $color,
        $value in $theme-colors {
            &.hs-card-header-#{$color} {
                background-color: $value !important;
                color: set-textcolor($value) !important;
            }
        }

        &.hs-card-header-border {
            border-bottom: 2px solid $accent-contrast;
        }
    }

    .k-card-title {
        line-height: inherit;
    }
}

.hs-card-image-left img {
    width: 100%;
    padding: 0;
    float: clear;

    @media screen and (min-width: map-get($container-max-widths, "lg")) {
        float: left !important;
        padding-right: 1em;
        width: 50%;
    }
}

.hs-image-text-overlay {
    position: relative;
    img {
     width:100%;   
    }
    > div {
        position: absolute;
        top: 0;
        background-color: $accent-contrast;
        color: $white;
        opacity: .75;
        padding: 10px 10px;
        max-height: 92px;
        overflow: hidden;
    }
}