/* create some rules for ms teams dark mode, this is a first pass, will likely need several dozen more lines of code 
for different kendo widgets  */
$team-bg-default : #f5f5f5;
$team-bg-dark : #1f1f1f;
$team-color-dark: #9291AE;
$team-color-panel: #3A3852;

.hsbody {
    background-color: $team-bg-default;

    .hs-teams-bg-color {
        background-color: $team-bg-default;

        .k-content {
            background-color: $team-bg-default;
        }
    }

    .k-tabstrip {
        @extend .hs-tabstrip-shaded;
        @extend .hs-tabstrip-full;
    }
    .k-tabstrip ul.k-tabstrip-items {
        li.k-item {
            background-color: $team-color-dark;
            color: $white;

            &.k-state-active {
                background-color: $team-color-panel !important;
            }
        }
    }
}

.hsbody.v-contrast,
.hsbody.v-dark {
    background-color: $team-bg-dark;

    .hs-teams-bg-color {
        background-color: $team-bg-dark;

        .k-content {
            background-color: $team-bg-dark;
        }
    }

    .k-card,
    .k-listview-content {
        background-color: $team-color-panel !important;
        color: $team-color-dark !important;
    }

    .k-tabstrip li.k-item:not(.k-state-active) {
        background-color: $team-color-dark !important;
    }

    // make all the bootstrap text variants the same color in dark/contrast mode 
    @each $color,
    $value in $theme-colors {
        .text-#{$color} {
            color: $team-color-dark !important;
        }
    }
}

.hsbody.v-dark .k-scheduler * {
    background-color: $team-color-panel !important;
    border-color: $team-bg-dark;

    .k-header {
        background-color: $team-color-panel !important;
        border-color: $team-bg-dark;
    }

    .k-scheduler-table tr:hover *,
    .k-state-hover {
        background-color: $team-color-dark !important;
        color: $white;
        border-color: $team-color-panel !important;
    }
}