// Handshake Helper classes for kendo web fonts. 

$stack-size: 1.3em;

@each $color,
$value in $theme-colors {
    &.hs-icon-#{$color} {
        color: $value;
    }
}

.hs-icons-stack {
    position: relative;
    display: inline-block;
    height: $stack-size;
    line-height: $stack-size;
    vertical-align: middle;
    width: $stack-size;
    font-size: $stack-size;

    &.hs-icons-stack-2 {
        font-size: $stack-size * 1.75;
    }

    >.k-icon {
        position: absolute;
        left: 0;
        width: 100%;
        display: inline-block;
        text-align: center;
    }

    >.hs-stack-2x {
        font-size: $stack-size;
    }

    >.hs-stack-1x {
        line-height: inherit;
        font-size: $stack-size * 0.5;
    }

    >.hs-stack-bottom-right {
        font-size: $stack-size * 0.45;
        top: $stack-size * 0.70;
        left: $stack-size * 0.15;
    }

    >.hs-stack-top-left {
        font-size: $stack-size * 0.45;
        bottom: $stack-size * 0.70;
        left: $stack-size * -0.15;
    }

    >.hs-stack-top-right {
        font-size: $stack-size * 0.45;
        bottom: $stack-size * 0.70;
        left: $stack-size * 0.15;
    }

    >.hs-stack-bottom-left {
        font-size: $stack-size * 0.45;
        top: $stack-size * .70;
        left: $stack-size * -0.15;
    }
}